
import type { NextApiRequest, NextApiResponse } from 'next';
import https from 'https';

// SSL Ïù∏Ï¶ùÏÑú Í≤ÄÏ¶ù Ïö∞Ìöå (ÌöåÏÇ¨ ÎÑ§Ìä∏ÏõåÌÅ¨/ÌîÑÎ°ùÏãú ÌôòÍ≤Ω ÎåÄÏùë)
if (process.env.NODE_ENV === 'production' || process.env.NODE_ENV === 'development') {
    process.env.NODE_TLS_REJECT_UNAUTHORIZED = '0';
}

type Data = {
    success?: boolean;
    error?: string;
};

console.log('[Contact API] Module Loaded');

export default async function handler(
    req: NextApiRequest,
    res: NextApiResponse<Data>
) {
    console.log('[Contact API] Handler Triggered', req.method);
    if (req.method !== 'POST') {
        return res.status(405).json({ error: 'Method Not Allowed' });
    }

    try {
        const { message } = req.body;

        if (!message || typeof message !== 'string' || !message.trim()) {
            return res.status(400).json({ error: 'Î©îÏãúÏßÄÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.' });
        }

        const webhookUrl = process.env.DISCORD_WEBHOOK_URL;

        if (!webhookUrl) {
            console.error('[Contact API] DISCORD_WEBHOOK_URL ÌôòÍ≤ΩÎ≥ÄÏàòÍ∞Ä ÏÑ§Ï†ïÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.');
            return res.status(500).json({ error: 'ÏÑúÎ≤Ñ ÏÑ§Ï†ï Ïò§Î•ò' });
        }

        const discordPayload = {
            embeds: [
                {
                    title: 'üì¨ ZED ÏõπÏÇ¨Ïù¥Ìä∏ ÏÉà Î¨∏Ïùò',
                    description: message.trim(),
                    color: 0x14b8a6,
                    timestamp: new Date().toISOString(),
                    footer: {
                        text: 'ZED Contact Form',
                    },
                },
            ],
        };

        const discordResponse = await fetch(webhookUrl, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(discordPayload),
        });

        if (!discordResponse.ok) {
            const errorText = await discordResponse.text();
            console.error('[Contact API] Discord Ï†ÑÏÜ° Ïã§Ìå®:', errorText);
            return res.status(500).json({ error: 'Discord Ï†ÑÏÜ° Ïã§Ìå®' });
        }

        // ÏÑ±Í≥µ
        console.log('[Contact API] Î©îÏãúÏßÄ Ï†ÑÏÜ° ÏÑ±Í≥µ');
        return res.status(200).json({ success: true });

    } catch (error) {
        console.error('[Contact API] Ïò§Î•ò:', error);
        return res.status(500).json({ error: 'ÏÑúÎ≤Ñ Ïò§Î•ò' });
    }
}
